// components/ProjectStartupGuide.tsx
"use client";

import { projectSteps } from "./data/projectSteps";
import { HeroSection } from "./HeroSection";
import { StepsGrid } from "./StepsGrid";

export function ProjectStartupGuide() {
  const handleGetStarted = () => {
    // Scroll to steps section
    document.getElementById("steps")?.scrollIntoView({ behavior: "smooth" });
  };

  return (
    <div className="bg-white">
      <HeroSection
        title="Project Startup Guide"
        subtitle="A comprehensive 9-step roadmap to take your project from idea to launch."
        ctaText="Explore Steps"
        onCtaClick={handleGetStarted}
      />

      <div id="steps" className="bg-gray-50">
        <StepsGrid steps={projectSteps} />
      </div>
    </div>
  );
}