// components/StepsGrid.tsx
import { ProjectStep } from "./data/projectSteps";
import { StepCard } from "./StepCard";
import {LayoutWrapper} from "./LayoutWrapper";

interface StepsGridProps {
  steps: ProjectStep[];
}

export function StepsGrid({ steps }: StepsGridProps) {
  return (
    <LayoutWrapper className="py-12 md:py-16">
      <div className="mx-auto max-w-2xl text-center mb-12">
        <h2 className="text-3xl font-bold tracking-tight text-gray-900 sm:text-4xl">
          Your Project Roadmap
        </h2>
        <p className="mt-4 text-lg text-gray-600">
          Follow these 9 essential steps to launch your project successfully.
        </p>
      </div>

      <div className="grid grid-cols-1 gap-6 sm:grid-cols-2 lg:grid-cols-3">
        {steps.map((step) => (
          <StepCard
            key={step.id}
            step={step.id}
            title={step.title}
            description={step.description}
            icon={step.icon}
          />
        ))}
      </div>
    </LayoutWrapper>
  );
}